#searchForm {
    display: flex;
    justify-content: center;
    align-content: center;
    max-height: 100%;
    margin-bottom: 18%;
    gap: 15px;
}

#searchLabel {
    color: rgb(131, 121, 121);
    display: flex;
    justify-content: center;
    font-size: 4.5em;
}

#inputBox {
    display: flex;
    align-self: center;
    font-size: 2em;
    height: 50%;
    width: 60%;
    outline: none;
    border: 1px solid #DDDDDD;
    border-radius: 10px;
    transition: all 0.3s;
    color: rgb(31, 199, 177);
    border: 2px solid rgb(31, 199, 177);
    background-color: rgb(87, 77, 77);

}

#inputBox:focus {
    box-shadow: 0 0 5px rgb(31, 199, 177);
    padding: 3px 0px 3px 3px;
    margin: 5px 1px 3px 0px;
    background-color: #141212;
}

#randomContent {
    color: rgb(131, 121, 121);
    display: flex;
    justify-content: center;
    align-content: center;
    flex-wrap: wrap;
    font-size: 1.2em;
}

#randomizer {
    display: flex;
    justify-content: center;
    align-self: center;
    flex-basis: 100%;
    text-decoration: none;
    color: rgb(31, 199, 177);
    margin: 0 45%;
    background-color: #2b2727;
    border-radius: 20px;
    padding: 0.5em;
    letter-spacing: 1.6px;
    transition: all 0.5s;
}

#randomizer:hover {
    background-color: rgb(11, 10, 10);
    transform: scale(1.1);
}

@media (max-width: 500px) {
    #inputBox {
        font-size: 3vmax;
    }

    #randomContent {
        font-size: 2.6vmax;
    }

    #randomizer {
        font-size: 5vmax;
    }

}